@import "color.less";

@normal-height: 350px;
@selected-height: 375px;

.selection-panel-container {
  position: relative;
  padding: 10px;
  height: @normal-height;
  margin-bottom: 25px;
  
  background-color: #FFF;
  text-align: center;
  border-radius: 0 0 5px 5px;

  border-right: 1px solid rgba(50,50,50,0.2);
  border-left: 1px solid rgba(50,50,50,0.2);
  border-bottom: 1px solid rgba(50,50,50,0.2);
  border-top: 0px transparent;

  -webkit-box-shadow: 0 30px 70px -9px rgba(50,50,50,0.2);
  -moz-box-shadow: 0 30px 70px -9px rgba(50,50,50,0.2);
  box-shadow: 0 30px 70px -9px rgba(50,50,50,.2);

  .selection-panel-image {
    width: 140px;
    height: 140px;
    margin: 10px;
  }

  .selection-panel-title {
    display: block;
    font-weight: bold;
    text-transform: uppercase;
  }

  ::ng-deep nbp-button {
    position: absolute;
    bottom: 20px;
    left: 0px;
    right: 0px;
  }

  ::ng-deep nbp-button .btn.button.secondary.select {
    color: @green;
    border-color: @green !important;
  }

  ::ng-deep nbp-button .btn.button.secondary.select:disabled {
    opacity: 1!important;
    background-color: #fff !important;
    color: #ccc;
    border-color: @gray !important;
  }

  &.selected {
    height: @selected-height;
    margin-bottom: 0px;

    border-right: 1px solid rgba(50, 50, 50,0.1);
    border-left: 1px solid rgba(50, 50, 50,0.1);
    border-bottom: 1px solid rgba(50, 50, 50,0.1);
    border-top: 0px transparent;

    -webkit-box-shadow: 0 30px 60px -30px rgba(127,201,31,0.7);
    -moz-box-shadow: 0 30px 60px -30px rgba(127,201,31,0.7);
    box-shadow: 0 30px 60px -30px rgba(127,201,31,0.7);

    /deep/ nbp-button {
      bottom: 20px + (@selected-height - @normal-height);
    }
  }
}
